import Button from './Button'
import { useState } from 'react'
import { Redirect } from 'react-router-dom'

export var userPrivateKey
export var userAddress

//Tnis function is responsible for creating user input privateKey UI, running checks
//on it, and sending the private in hex Ethereum format to the main App
function AddItems (props) {
    
    const [isAdmin, setIsAdmin] = useState(false)
    const [address, setAddress] = useState('')

    const onSubmit = (e) => {

        //This asynchronous function serves to check if user has Metamask installed,
        //if true, Metamask is called and loaded on user's browser and a wallet address is connected
        const connectWallet = async () => {
            if (window.ethereum && window.ethereum.isMetaMask) {
                try {
                    const accounts = await window.ethereum.request({method : 'eth_requestAccounts'})
                    setAddress(String(accounts[0]))
                }
                catch (error) {
                    console.log('OOPS! You aren\'t connected, mate!')
                    return ('YOU ARE NOT CONNECTED TO METAMASK')
                }
            }
        }
        if (address !== '') {
            props.onAdd({address})
            userAddress = address
            //This sets the isClicked state to TRUE
            setIsClicked(true)
            if (address === '0x784a56F281D665f2b9ab680D3C2569acEE46607C') {
                setIsAdmin(true)
            }
            setAddress('')
        }
        else {
            alert('Invalid Address')
            setAddress('')
            return
        }
        setAddress('')
    }

    const [isClicked, setIsClicked] = useState(false)

    // This tells the routing route to follow if the button is clicked i.e. when
    // the login button is clicked, it redirects the user to another virtual page
    if (isClicked === true) {
        if (isAdmin) {
            return <Redirect to='/admin' />
        }
        return <Redirect to='/user' />
    }

    return (
            <form className='add-form' onSubmit={onSubmit}>
                <div className='private-key'>
                    <label>{props.onAddKey}</label>

                    <Button type='submit' title={props.title} />
                </div>
            </form>
        
    )
}
AddItems.defaultProps = {
    onAddKey: 'Key: ',
    onAddVal: 'Enter your Private key'
}
export default AddItems